import { useState } from "react";
import "./ExpenseForm.css";

const ExpenseForm = (props) => {
  const [newTitle, setNewTitle] = useState("");
  const [enteredAmount, setEnteredAmount] = useState("");
  const [enteredDate, setEnteredDate] = useState("");
  //                                      Alternative way of useState:
  //   const [userInput, setUserInput] = useState({
  //     newTitle: "",
  //     newAmount: "",
  //     newDate: "",
  //   });

  const titleChangeHandler = (event) => {
    setNewTitle(event.target.value);
    //             Alternative way of useState(depending on previous useState value):
    // setNewTitle((prevState) => {
    //   return { ...prevState, newTitle: event.target.value };
    // });
  };
  const amountChangeHandler = (event) => {
    setEnteredAmount(event.target.value);
    //             Alternative way of useState(depending on previous useState value):
    // setNewAmount((prevState) => {
    //   return { ...prevState, newAmount: event.target.value };
    // });
  };
  const dateChangeHandler = (event) => {
    setEnteredDate(event.target.value);
    //             Alternative way of useState(depending on previous useState value):
    // setNewDate((prevState) => {
    //   return { ...prevState, newDate: event.target.value };
    // });
  };

  const submitHandler = (event) => {
    event.preventDefault();

    const expenseData = {
      title: newTitle,
      amount: +enteredAmount,
      date: new Date(enteredAmount),
    };

    props.onSaveExpenseData(expenseData);
    setNewTitle("");
    setEnteredAmount("");
    setEnteredDate("");
  };

  return (
    <form onSubmit={submitHandler}>
      <div className="new-expense__controls">
        <div className="new-expense__control">
          <label>Title</label>
          <input type="text" value={newTitle} onChange={titleChangeHandler} />
        </div>
        <div className="new-expense__control">
          <label>Amount</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={enteredAmount}
            onChange={amountChangeHandler}
          />
        </div>
        <div className="new-expense__control">
          <label>Date</label>
          <input
            type="date"
            min="2020-01-01"
            max="2022-12-01"
            value={enteredDate}
            onChange={dateChangeHandler}
          />
        </div>
      </div>
      <div className="new-expense__actions">
        <button type="button" onClick={props.onCancel}>
          Cancel
        </button>
        <button type="submit">Add Expense</button>
      </div>
    </form>
  );
};

export default ExpenseForm;
